import React from "react";

import Plot from "react-plotly.js";

import styles from "./TailPlot.module.css";

import inverted_data from "../assets/inverted.json";
import plot_data from "../assets/plot.json";

const TailPlot = (props) => {
  const current_combination = props.current;
  const percentage = inverted_data[current_combination];

  return (
    <div className={styles.container}>
      <div className={styles.tip}>(Backspace to reset)</div>
      {percentage !== undefined ? (
        <div className={styles.percentages}>
          <div className={styles.one}>
            1 rows {((100 * plot_data[0][percentage]) / 36).toFixed(0)}%
          </div>
          <div className={styles.two}>
            2 rows {((100 * plot_data[1][percentage]) / 36).toFixed(0)}%
          </div>
          <div className={styles.three}>
            3 rows {((100 * plot_data[2][percentage]) / 36).toFixed(0)}%
          </div>
          <div className={styles.percentile}>
            Percentile{" "}
            {(100 - (100 * percentage) / plot_data[0].length).toFixed(2)}%
          </div>
        </div>
      ) : (
        <div className={styles.percentages}></div>
      )}
      
      <Plot
        data={[
          {
            y: plot_data[0],
            mode: "lines",
            name: "1 rows",
            fill: "tozeroy",
            fillcolor: "#e0d5cd",
            line: { color: "#e0d5cd" },
          },
          {
            y: plot_data[1],
            mode: "lines",
            name: "2 rows",
            fill: "tozeroy",
            fillcolor: "#bfc082",
            line: { color: "#bfc082" },
          },
          {
            y: plot_data[2],
            mode: "lines",
            name: "3 rows",
            fill: "tozeroy",
            fillcolor: "#c3a740",
            line: { color: "#c3a740" },
          },
          percentage
            ? {
                mode: "lines+text",
                x: [percentage, percentage],
                y: [0, 36],
                line: {
                  color: "black",
                  width: 2.5,
                },
              }
            : {},
        ]}
        layout={{
          width: 800,
          height: 200,
          xaxis: {
            zeroline: false,
            showline: false,
            showticklabels: false,
            showgrid: false,
            range: [0, plot_data[0].length],
          },
          yaxis: {
            zeroline: false,
            showline: false,
            showticklabels: false,
            showgrid: false,
            range: [0, 36],
          },
          showlegend: false,
          margin: {
            autoexpand: false,
            b: 0,
            t: 0,
            l: 0,
            r: 0,
          },
        }}
        config={{ staticPlot: true }}
      />
    </div>
  );
};

export default TailPlot;
