import React, { useEffect, useState } from "react";
import { GlobalHotKeys } from "react-hotkeys";
import TailButton from "./TailButton";

import styles from "./TailPage.module.css";

const keyMap = {
  KEY_1: "1",
  KEY_2: "2",
  KEY_3: "3",
  KEY_4: "4",
  KEY_5: "q",
  KEY_6: "w",
  KEY_7: "e",
  KEY_8: "r",
  KEY_9: "a",
  KEY_10: "s",
  KEY_11: "d",
  KEY_12: "f",
  KEY_13: "z",
  KEY_14: "x",
  KEY_15: "c",
  KEY_16: "v",
  KEY_RESET: "backspace",
};

const hotkeys = [
  keyMap.KEY_1,
  keyMap.KEY_2,
  keyMap.KEY_3,
  keyMap.KEY_4,
  keyMap.KEY_5,
  keyMap.KEY_6,
  keyMap.KEY_7,
  keyMap.KEY_8,
  keyMap.KEY_9,
  keyMap.KEY_10,
  keyMap.KEY_11,
  keyMap.KEY_12,
  keyMap.KEY_13,
  keyMap.KEY_14,
  keyMap.KEY_15,
  keyMap.KEY_16,
];

const TailPage = (props) => {
  const [buttons, setButtons] = useState(hotkeys.map((_, index) => !!((props.value >> (15 - index)) & 1)));

  useEffect(() => {
    const popcount = buttons.reduce((acc, x) => acc + x);
    const bits = buttons.reduce((acc, x) => (acc << 1) | x);

    props.onChange(bits, popcount);
  }, [buttons, props]);

  const toggleButton = (index) =>
    setButtons((b) => {
      const new_list = [...b];
      new_list[index] = !b[index];
      return new_list;
    });

  const keyPressHandler = {
    KEY_1: () => toggleButton(0),
    KEY_2: () => toggleButton(1),
    KEY_3: () => toggleButton(2),
    KEY_4: () => toggleButton(3),
    KEY_5: () => toggleButton(4),
    KEY_6: () => toggleButton(5),
    KEY_7: () => toggleButton(6),
    KEY_8: () => toggleButton(7),
    KEY_9: () => toggleButton(8),
    KEY_10: () => toggleButton(9),
    KEY_11: () => toggleButton(10),
    KEY_12: () => toggleButton(11),
    KEY_13: () => toggleButton(12),
    KEY_14: () => toggleButton(13),
    KEY_15: () => toggleButton(14),
    KEY_16: () => toggleButton(15),
    KEY_RESET: () => setButtons(Array(16).fill(false))
  };

  const onPressHandler = (index) => () => {
    toggleButton(index);
  };

  return (
    <div className={styles.page}>
      <GlobalHotKeys keyMap={keyMap} handlers={keyPressHandler} />
      {hotkeys.map((key, index) => (
        <TailButton
          className={styles.stamp}
          key={index}
          id={index}
          hotkey={key.toUpperCase()}
          value={buttons[index]}
          onClick={onPressHandler(index)}
        />
      ))}
    </div>
  );
};

export default TailPage;
