import { useState } from "react";
import styles from "./App.module.css";
import TailPage from "./components/TailPage";
import TailPlot from "./components/TailPlot";

function App() {
  const urlParams = new URLSearchParams(window.location.search);

  const [bits, setBits] = useState(urlParams.get("p"));
  const [popcount, setPopcount] = useState(0);

  const pageOnChangeHandler = (bits, popcount) => {
    setBits(bits);
    setPopcount(popcount);

    window.history.replaceState(null, "", `?p=${bits}`);
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Wondrous Tails</h1>
      <div className={styles.book}>
        <div className={styles.ear}></div>
        <TailPage value={bits} onChange={pageOnChangeHandler} />
        <div className={`${styles.ear} ${styles.count}`}>
          <span className={styles.big}>{popcount}</span>
          <span className={styles.small}>/7</span>
        </div>
      </div>
      <TailPlot current={bits} />
    </div>
  );
}

export default App;
