import React from "react";

import styles from "./TailButton.module.css";

const TailButton = (props) => {
  const id = props.id;
  const hotkey = props.hotkey;

  const onClickHandler = () => {
    props.onClick();
  };

  return (
    <button
      className={`${props.className} ${styles.button} ${
        props.value ? styles.active : styles.inactive
      } ${!(id % 2) ^ !!(Math.floor(id / 4) % 2)  ? styles.dark : styles.light}`}
      onClick={onClickHandler}
    >
      {hotkey}
    </button>
  );
};

export default TailButton;
